<template>
<v-ons-page>
  <div class="rank">
    <h1><router-link to="/">EMOTION COMPETITION</router-link></h1>
    <h2>Ranking</h2>
    <b-container class="rankContainer">
    <b-row>
      <b-col vertical-align='center'>
        <h3>Total</h3>
      </b-col>
    </b-row>
    <b-row  v-for="(item, index) in overAllRank" :key="item.userId">
      <b-col cols="2" sm="3" class="num">
        {{ index + 1 }}: 
      </b-col>
      <b-col cols="4" sm="4" class="name">
        {{ convertUserName(item.userId) }}
      </b-col>
      <b-col cols="6" sm="5" class="score">
         : {{ Math.floor(item.sum * 10000)/100 }} Pt
      </b-col>
    </b-row>
    <br>
    <br>
    <b-row>
      <b-col vertical-align='center'>
        <h3>happiness</h3>
      </b-col>
    </b-row>
    <b-row  v-for="(item, index) in smileRank" :key="item.userId">
      <b-col cols="2" sm="3" class="num">
        {{ index + 1 }}: 
      </b-col>
      <b-col cols="4" sm="4" class="name">
        {{ convertUserName(item.userId) }}
      </b-col>
      <b-col cols="6" sm="5" class="score">
         : {{ Math.floor(item.happiness * 10000)/100 }} Pt
      </b-col>
    </b-row>
    <b-row>
      <b-col vertical-align='center'>
        <h3>surpirse</h3>
      </b-col>
    </b-row>
    <b-row  v-for="(item, index) in surpriseRank" :key="item.userId">
      <b-col cols="2" sm="3" class="num">
        {{ index + 1 }}: 
      </b-col>
      <b-col cols="4" sm="4" class="name">
        {{ convertUserName(item.userId) }}
      </b-col>
      <b-col cols="6" sm="5" class="score">
         : {{ Math.floor(item.surprise * 10000)/100 }} Pt
      </b-col>
    </b-row>
      <!-- <b-row v-for="(item, index) in surpriseRank" :key="item.userId">
      <b-col vertical-align='center'>
        {{ index + 1 }} : {{ item.userId }} : {{ Math.floor(item.surprise * 10000)/100 }} Pt
      </b-col>
    </b-row> -->
    <b-row>
      <b-col vertical-align='center'>
        <h3>neutral</h3>
      </b-col>
    </b-row>
    <b-row  v-for="(item, index) in neutralRank" :key="item.userId">
      <b-col cols="2" sm="3" class="num">
        {{ index + 1 }}: 
      </b-col>
      <b-col cols="4" sm="4" class="name">
        {{ convertUserName(item.userId) }}
      </b-col>
      <b-col cols="6" sm="5" class="score">
         : {{ Math.floor(item.neutral * 10000)/100 }} Pt
      </b-col>
    </b-row>
    <!-- <b-row v-for="(item, index) in neutralRank" :key="item.userId">
      <b-col vertical-align='center'>
        {{ index + 1 }} : {{ item.userId }} : {{ Math.floor(item.neutral * 10000)/100 }} Pt
      </b-col>
    </b-row> -->
    <!-- <b-row v-for="(item, index) in overAllRank" :key="item.userId">
      <b-col vertical-align='center'>
        {{ index + 1 }} : {{ item.userId }} : {{ Math.floor(item.sum * 10000)/100 }} Pt
      </b-col>
    </b-row> -->

    <b-row class="back">
      <b-col vertical-align='center'>
        <router-link to="/">◀︎BACK</router-link>
      </b-col>
    </b-row>
    </b-container>
  </div>
</v-ons-page>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
export default {
  components: {
    PulseLoader
  },
  name: 'hello1',
  data () {
    return {
      list: [],
      smileRank: [],
      surpriseRank: [],
      neutralRank: [],
      overAllRank: []
    }
  },
  computed: {
  },
  mounted () {
    const url = 'https://ldvqd6fhxk.execute-api.ap-northeast-1.amazonaws.com/prod/result/today'
    $.ajax({
      url: url,
      type: 'GET',
      dataType: 'json',
      scriptCharset: 'UTF-8'
    })
      .done((data) => {
        console.log(data)
        data.sort((a, b) => {
          if (a.happiness > b.happiness) return -1
          if (a.happiness < b.happiness) return 1
          return 0
        })
        Object.assign(this.$data.smileRank, data)
        if (this.$data.smileRank.length > 3) {
          this.$data.smileRank = this.$data.smileRank.slice(0, 3)
        } else {
          while (this.$data.smileRank.length < 3) {
            this.$data.smileRank.push({'-': 0})
          }
        }
        data.sort((a, b) => {
          if (a.surprise > b.surprise) return -1
          if (a.surprise < b.surprise) return 1
          return 0
        })
        Object.assign(this.$data.surpriseRank, data)
        if (this.$data.surpriseRank.length > 3) {
          this.$data.surpriseRank = this.$data.surpriseRank.slice(0, 3)
        } else {
          while (this.$data.surpriseRank.length < 3) {
            this.$data.surpriseRank.push({'-': 0})
          }
        }
        data.sort((a, b) => {
          if (a.neutral > b.neutral) return -1
          if (a.neutral < b.neutral) return 1
          return 0
        })
        Object.assign(this.$data.neutralRank, data)
        if (this.$data.neutralRank.length > 3) {
          this.$data.neutralRank = this.$data.neutralRank.slice(0, 3)
        } else {
          while (this.$data.neutralRank.length < 3) {
            this.$data.neutralRank.push({'-': 0})
          }
        }
        data.sort((a, b) => {
          if (a.sum > b.sum) return -1
          if (a.sum < b.sum) return 1
          return 0
        })
        this.$data.overAllRank = data
        if (this.$data.overAllRank.length > 3) {
          this.$data.overAllRank = this.$data.overAllRank.slice(0, 3)
        } else {
          while (this.$data.overAllRank.length < 3) {
            this.$data.overAllRank.push({'-': 0})
          }
        }
      })
      .fail((jqXHR, textStatus, errorthrowm) => {
      })
  },
  methods: {
    convertUserName: function (userName) {
      console.log(userName)
      if (userName === undefined || userName === '') {
        return 'undefind'
      }
      return userName
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('https://fonts.googleapis.com/css?family=Monoton|Press+Start+2P');
h1 {
  font-family: 'Monoton' !important;
  font-size: 2.4rem !important;
  margin-top: 0.2rem !important;
  margin-bottom: 0.2em !important;
}
h2, h3, .back {
  font-family: 'Press Start 2P' !important;
  font-weight: normal;
}
h3 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}
.back {
  margin-top:1.5rem;
}

.num {
  text-align: right;
  padding-right: 5px;
}
.name {
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
}

.score {
  text-align: left;
  padding-left: 5px;
}
.rank {
  font-family: 'Press Start 2P' !important;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}

.load {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  margin: auto;

  width: 128px;
  height: 64px;
}

.rankContainer {
  max-width: 800px !important;
}
</style>



