<template>
<v-ons-page>
  <h1><router-link to="/">EMOTION COMPETITION</router-link></h1>
  <audio id="bgm" name="bgm" :src='bgm' autoplay loop></audio>
  <audio id="shot" name="shot" :src='shot'></audio>
  <div class="camera" v-show='count < 3'>
    <v-ons-row>
      <v-ons-col vertical-align='center'>
        <video v-show="videoflg" ref="video" width="50%" autoplay playsinline></video>
        <canvas v-show="canvasflg" ref="canvas" id="canvas"></canvas>
        <div class="load" v-show="canvasflg">
          <pulse-loader loading="loading" color="#90ee90" size="40px"></pulse-loader>
        </div>
      </v-ons-col>
    </v-ons-row>
    <v-ons-row>
      <v-ons-col vertical-align='center'>
        <h3>{{emotion[gameEmotionList[count]]}}!</h3>
      </v-ons-col>
    </v-ons-row>
    <v-ons-row>
      <v-ons-col vertical-align='center'>
        <div v-show="snapcount !== 4" class = "snapcount"><h3>{{ snapcount }}</h3></div>
        <div v-show="snapcount === 4" class = "snapcount"><h3>ready?</h3></div>
      </v-ons-col>
    </v-ons-row>
    <v-ons-row>
      <v-ons-col vertical-align='center'>
        <v-ons-button :disabled='!gameflg' id="snap" v-on:click='capturesFunc()'>START</v-ons-button>
      </v-ons-col>
    </v-ons-row>
    <v-ons-row>
      <v-ons-col vertical-align='center'  class="back">
        <router-link to="/">◀︎BACK</router-link>
      </v-ons-col>
    </v-ons-row>
  </div>
  <div v-show="count > 2" class="result">
    <h2>Result</h2>
    <b-container class="resultRow">
    <b-row v-for="(item,index) in emotionArr" v-bind:key="index">
      <b-col class="score">
        {{ emotion[emotionNumArr[index]] }}： {{Math.floor(item[emotion[emotionNumArr[index]]] * 10000)/100}} / 100 Pt
      </b-col>
    </b-row>
    <br>
    <b-row>
      <b-col>
        <input type="text" maxlength="7" v-model="userId" pattern="/^[a-zA-Z0-9]+$/" placeholder="INPUT YOUR USERNAME" />
      </b-col>
    </b-row>
    <b-row>
      <b-col>
      <v-ons-button id="resist" v-on:click='send()'>REGISTER</v-ons-button>
      </b-col>
    </b-row>
    <br>
    </b-container>
    <v-ons-row>
      <v-ons-col vertical-align='center' >
        <v-ons-button id="retry" v-on:click='retry()'>RETRY</v-ons-button>
      </v-ons-col>
    </v-ons-row>
    <v-ons-row>
      <v-ons-col vertical-align='center' class="back">
        <router-link to="/">◀︎BACK</router-link>
      </v-ons-col>
    </v-ons-row>
  </div>
</v-ons-page>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import backgroundbgm from '@/bgm/NewWorld.mp3'
import shotbgm from '@/bgm/beamgun-shot1.mp3'
export default {
  components: {
    PulseLoader
  },
  name: 'hello1',
  data () {
    return {
      bgm: backgroundbgm,
      shot: shotbgm,
      muted: false,
      video: {},
      canvas: {},
      captures: [],
      gameflg: true,
      videoflg: true,
      canvasflg: false,
      emotionArr: [],
      count: 0,
      emotion_kana: ['怒り顔', '侮辱顔', '嫌悪の顔', '怖がった顔', '幸せ顔', '無表情', '悲しい顔', '驚いた顔'],
      emotion: ['anger', 'contempt', 'disgust', 'fear', 'happiness', 'neutral', 'sadness', 'surprise'],
      /* 幸せ→驚き→無表情の順で実施 */
      gameEmotionList: [4, 7, 5],
      emotionNumArr: [],
      userId: null,
      snapcount: 4
    }
  },
  mounted () {
    this.video = this.$refs.video
    const medias = {audio: false, video: true}
    navigator.getUserMedia(medias, this.successCallback, this.errorCallback)
  },
  watch: {
    muted: function (val) {
      let bgm = document.getElementById('bgm')
      if (val) {
        bgm.pause()
      } else {
        bgm.play()
      }
    },
    count: function (val) {
      if (val > 2) {
        for (let catpureData of this.$data.captures) {
          this.calcScore(catpureData)
        }
      }
    }
  },
  methods: {
    /**
     * ループ関数
     * 任意の感覚で特定関数を実行する
     */
    loopSleep (_loopLimit, _interval, _mainFunc) {
      var loopLimit = _loopLimit
      var interval = _interval
      var mainFunc = _mainFunc
      var i = 0
      var loopFunc = function () {
        var result = mainFunc(i)
        if (result === false) {
          // break機能
          return
        }
        i = i + 1
        if (i < loopLimit) {
          setTimeout(loopFunc, interval)
        }
      }
      loopFunc()
    },
    /**
     * スタートボタン押下時関数
     * 3回の撮影実行関数を制御する
     */
    capturesFunc () {
      this.$data.gameflg = false
      this.loopSleep(12, 1500, this.capture)
      // for (let catpureData in this.$data.captures) {
      //   this.calcScore(catpureData)
      // }
    },
    /**
     * スコア計算関数
     */
    calcScore (captureData) {
      var params = {
        returnFaceId: 'true',
        returnFaceLandmarks: 'false',
        returnFaceAttributes:
            'age,gender,headPose,smile,facialHair,glasses,emotion,' +
            'hair,makeup,occlusion,accessories,blur,exposure,noise'
      }
      var that = this
      $.ajax({
        // NOTE: You must use the same location in your REST call as you used to obtain your subscription keys.
        // url: 'https://westus.api.cognitive.microsoft.com/emotion/v1.0/recognize?' + $.param(params),
        url: 'https://eastasia.api.cognitive.microsoft.com/face/v1.0/detect?' + $.param(params),
        beforeSend: function (xhrObj) {
          // Request headers, also supports "application/octet-stream"
          xhrObj.setRequestHeader('Content-Type', 'application/octet-stream')
          // NOTE: Replace the "Ocp-Apim-Subscription-Key" value with a valid subscription key.
          // xhrObj.setRequestHeader('Ocp-Apim-Subscription-Key', '4dfe28f1041d48e1a36f469e67ec93d6')
          xhrObj.setRequestHeader('Ocp-Apim-Subscription-Key', 'b327acc0953a4077966213de5c054bbb')
        },
        type: 'POST',
        // Request body
        data: this.makeblobdata(captureData),
        processData: false,
        async: false
      }).done(function (data) {
        console.log(data)
        that.$data.emotionArr.push(data[0].faceAttributes.emotion)
        that.$data.emotionNumArr.push(that.$data.gameEmotionList[that.$data.count])
        // that.$data.count = that.$data.count + 1
        that.$data.canvasflg = false
        that.$data.videoflg = true
      }).fail(function (err) {
        alert('Please try again')
        console.log(err)
        that.$data.canvasflg = false
        that.$data.videoflg = true
      })
    },
    /**
     * インカメラ用コールバック関数
     */
    successCallback (stream) {
      this.video.srcObject = stream
    },
    /**
     * インカメラ用コールバック関数
     */
    errorCallback (err) {
      alert(err)
    },
    /**
     * blob形式変換関数（本来は外出しすべき関数）
     */
    makeblobdata (dataURL) {
      const BASE64_MARKER = ';base64,'
      var parts
      var contentType
      var raw
      var rawLength
      if (dataURL.indexOf(BASE64_MARKER) === -1) {
        parts = dataURL.split(',')
        contentType = parts[0].split(':')[1]
        raw = decodeURIComponent(parts[1])
        return new Blob([raw], { type: contentType })
      }
      parts = dataURL.split(BASE64_MARKER)
      contentType = parts[0].split(':')[1]
      raw = window.atob(parts[1])
      rawLength = raw.length

      var uInt8Array = new Uint8Array(rawLength)

      for (var i = 0; i < rawLength; ++i) {
        uInt8Array[i] = raw.charCodeAt(i)
      }
      return new Blob([uInt8Array], { type: contentType })
    },
    /**
     * キャプチャ関数
     */
    capture () {
      this.$data.snapcount -= 1
      if (this.$data.snapcount !== 0) {
        return
      }
      let bgm = document.getElementById('shot')
      bgm.play()
      this.canvas = this.$refs.canvas
      var w = this.video.offsetWidth
      var h = this.video.offsetHeight
      this.canvas.setAttribute('width', w)
      this.canvas.setAttribute('height', h)
      this.$data.canvasflg = true
      this.canvas.getContext('2d').drawImage(this.video, 0, 0, w, h)
      this.$data.videoflg = false
      this.captures.push(this.canvas.toDataURL())
      this.$data.emotionNumArr.push(this.$data.gameEmotionList[this.$data.count])
      this.$data.count = this.$data.count + 1
      this.$data.canvasflg = false
      this.$data.videoflg = true
      this.$data.snapcount = 4
    },
    /**
     * リトライボタン押下時関数
     */
    retry () {
      this.$data.count = 0
      this.$data.captures = []
      this.$data.emotionNumArr = []
      this.$data.emotionArr = []
      this.$data.videoflg = true
      this.$data.canvasflg = false
      this.$data.gameflg = true
    },
    /**
     * スコア登録関数
     */
    send () {
      if (this.$data.userId === null || this.$data.userId === '') {
        alert('登録名を入力してください')
        return
      } else if (!this.$data.userId.match(/^[A-Za-z0-9]*$/)) {
        alert('登録名は半角英数字で入力してください')
        return
      }
      // 今日の日付で Date オブジェクトを作成
      let now = new Date()
      // 「年」「月」「日」「曜日」を Date オブジェクトから取り出してそれぞれに代入
      let y = now.getFullYear()
      let m = now.getMonth() + 1
      let d = now.getDate()
      if (m < 10) {
        m = '0' + m
      }
      if (d < 10) {
        d = '0' + d
      }
      let dateString = y + '-' + m + '-' + d
      let happiness = this.$data.emotionArr[0].happiness
      let surprise = this.$data.emotionArr[1].surprise
      let neutral = this.$data.emotionArr[2].neutral
      let sendData = {
        'userId': this.$data.userId,
        'date': dateString,
        'happiness': this.$data.emotionArr[0].happiness,
        'surprise': this.$data.emotionArr[1].surprise,
        'neutral': this.$data.emotionArr[2].neutral,
        'sum': happiness + surprise + neutral
      }
      var that = this
      $.ajax({
        url: 'https://ldvqd6fhxk.execute-api.ap-northeast-1.amazonaws.com/prod/',
        type: 'POST',
        dataType: 'json',
        data: JSON.stringify(sendData),
        scriptCharset: 'UTF-8',
        async: false,
        contentType: 'application/json'
      }).done((data) => {
        alert('登録完了')
        that.$data.count = 0
        that.$data.captures = []
        that.$data.emotionNumArr = []
        that.$data.emotionArr = []
        that.$data.videoflg = true
        that.$data.canvasflg = false
        that.$data.gameflg = true
      }).fail((err) => {
        console.log(err)
        alert('登録に失敗しました。再度顔をとってね')
        that.$data.count = 0
        that.$data.captures = []
        that.$data.emotionNumArr = []
        that.$data.emotionArr = []
        that.$data.videoflg = true
        that.$data.canvasflg = false
        that.$data.gameflg = true
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('https://fonts.googleapis.com/css?family=Monoton|Press+Start+2P');
h1 {
  font-family: 'Monoton' !important;
  font-size: 2.4rem !important;
  margin-top: 0.2rem !important;
  margin-bottom: 0.2em !important;
}
h2, h3, .result, .mute, .snapcount, .back{
  font-family: 'Press Start 2P' !important;
  font-weight: normal;
}

.resultRow {
  max-width:500px;
}
.back {
  margin-top:1.5rem;
}
h3 {
  margin-top: 0.3rem !important;
  margin-bottom: 0.3em !important;
}

#switch1 {
  float:left;
}
.mute {
  vertical-align: middle;
}
.camera #snap {
  font-family: 'Press Start 2P' !important;
}

#resist, #retry {
  font-family: 'Press Start 2P' !important;
}

input {
  font-family: 'Press Start 2P' !important;
  font-size: 1rem;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
.score {
  text-align: left;
}
.load {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  margin: auto;

  width: 128px;
  height: 64px;
}
</style>



