<template>
<v-ons-page>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2><router-link to="/1">GAME START</router-link></h2>
    <h2><router-link to="/rank">RANKING</router-link></h2>
  </div>
</v-ons-page>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'EMOTION COMPETITION'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('https://fonts.googleapis.com/css?family=Monoton|Press+Start+2P');
h1 {
  font-family: 'Monoton' !important;
  font-size: 2.4rem !important;
  margin-top: 0.2rem !important;
  margin-bottom: 0.2em !important;
}

h2 {
  font-family: 'Press Start 2P' !important;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



